*{
  margin: 0;
  padding: 0;
}

*::selection {
  color: none;
  background: none;
}

@keyframes social-transition {
  from {color: white;}
  to {color: #00faf2;}
}

@keyframes arrow-transition {
  from {color: grey;}
  to {color: #00faf2;}
}

@keyframes button-transition {
  from {color: #111; border-color: #111;}
  to {color: #00faf2; border-color: #00faf2;}
}

.App {
  text-align: center;
}

.page {
  height: 400vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 1);
  background-color: #333;
}

.content {
  width: 100vw;
  margin: 0 auto;
  height: calc(340vh + 60px);
}

.header {
  width: 100%;
  height: 50vh;
  text-align: center;
  cursor: default;
}

.header h3 {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 56px;
  font-stretch: expanded;
  font-weight: 900;
  letter-spacing: 4px;
  color: #fff;
  padding-top: calc(25vh - 80px);
}

.project-header {
  width: 100%;
  height: 50vh;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: black;
  cursor: default;
}

.site-nav {
  display: inline-block;
  list-style: none;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

.site-nav li {
  float: left;
  cursor: pointer;
  height: 100%;
  width: 50%;
  text-align: center;
}

.site-nav li:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.site-nav li h3 {
  font-size: 16px;
  font-family: 'Source Sans Pro', sans-serif;
  letter-spacing: 4px;
  line-height: 50px;
  color: #fff;
  height: 100%;
  width: 100%;
}

.project-header-nav {
  display: none;
}

.project-header-body {
  background-color: #fff;
  height: 50vh;
}

.project-grid {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  height: 290vh;
  background-color: #fff;
}

.project-grid :nth-child(4){
  border: none;
}

.project {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  text-align: center;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: #111;
}

.project h3 {
  height: 10%;
  padding-top: 10px;
  cursor: default;
  letter-spacing: 2px;
  font-family: 'Source Sans Pro', sans-serif;
}

.project ul {
  list-style: none;
  display: inline-block;
  height: 20%;
}

.project ul li {
  float: left;
  margin: 10px;
}

.project ul li a {
  border-style: solid;
  border-width: 2px;
  border-color: #111;
  padding: 6px;
  background-color: #fff;
  cursor: pointer;
  color: #111;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 16px;
  font-weight: bolder;
  letter-spacing: 1px;
  text-decoration: none;
}

.project ul li a:hover {
  border-color: #00faf2;
  color: #00faf2;
  animation-name: button-transition;
  animation-duration: 0.3s;
}

.project img {
  width: 80%;
  height: 50%;
}

.project p {
  height: 20%;
  width: 80%;
  margin: 4px auto;
  cursor: default;
  font-family: 'Merriweather', serif;
  line-height: 28px;
}

.project p::selection {
  background-color: #00faf2 !important;
}

.resumePage {
  width: 100%;
  height: 290vh;
  background-color: #fff;
}

.arrow {
  cursor: pointer;
  color: grey;
  width: 100%;
  float: center;
}

.arrow:hover {
  animation-name: arrow-transition;
  animation-duration: 1s;
  color: #00faf2;
}

.project-header-body {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  line-height: 10em;
}

.project-header-body h2 {
  font-size: 40px;
  font-family: 'Source Sans Pro', sans-serif;
  letter-spacing: 4px;
  text-transform: uppercase;
}

.social-nav {
  display: none;
}

.mobile-toggle {
  position: fixed;
  top: 0;
  right: 0;
  display: block;
  float: right;
  margin: 6px;
  background-color: rgba(0, 0, 0, 0);
  color: rgba(0,0,0,0.9);
  border-style: solid;
  border-width: 1px;
  border-color: rgba(0,0,0,0.9);
  border-radius: 50%;
  padding: 5px 7px;
  background-color: rgba(255,255,255,0.8);
}


.mobile-menu {
  display: block;
  height: 100vh;
  width: 250px;
  position: fixed;
  top: 0;
  right: 0;
  float: right;
  background-color: #fff;
  border-left-style: solid;
  border-left-width: 1px;
  border-left-color: #555;
  margin-right: -251px;
}

.menu-wrapper {
  width: calc(100% - 28px);
  margin: 14px auto;
}

.mobile-site-nav {
  margin-top: 30px;
  list-style: none;
}

.mobile-site-nav li {
  padding: 30px 0;
}

.mobile-site-nav li:nth-child(1){
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: rgb(150, 150, 150);
}

.mobile-site-nav li a {
  text-decoration: none;
  font-family: 'Source Sans Pro', sans-serif;
  color: #111;
  font-size: 18px;
  letter-spacing: 3px;
  font-weight: bolder;
}

.mobile-social-nav {
  display: inline-block;
  list-style: none;
}

.mobile-social-nav li {
  float: left;
  margin-right: 30px;
  border-style: solid;
  border-width: 1px;
  border-color: grey;
  border-radius: 100%;
  padding: 10px 13px;
}

.mobile-social-nav li a {
  text-decoration: none;
  color: grey;
}

.closeMenu {
  color: rgb(150, 150, 150);
  float: right;
  margin: 14px;
}

.footer {
  height: 10vh;
  background-color: rgb(220, 220, 220);
  display: grid;
  grid-template-columns: 1fr;
  cursor: default;
}

.footer div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 20px 40px;
  line-height: 20px;
}

.footer div h4::selection {
  background-color: #00faf2 !important;
  color: #fff !important;
}

.footer div h4:nth-child(1){
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 14px;
  letter-spacing: 2px;
  color: grey;
}

.footer div h4:nth-child(2){
  font-family: serif;
  font-size: 16px;
  font-weight: 100;
  font-style: italic;
  letter-spacing: 1px;
  color: rgb(130, 130, 130);
}

.footer-desktop-only {
  display: none !important;
}


@media only screen and (min-width: 750px){
  .page {
    height: 250vh;
    background-color: rgba(0, 0, 0, 0.3);
  }

  .mobile-menu {
    display: none;
  }
  .mobile-toggle{
    display: none;
  }

  .content {
    height: 200vh;
    width: 750px;
  }

  .project-header-nav {
    display: block;
    height: 60px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.2);
  }

  .header h3 {
    font-size: 60px;
    padding-top: calc(25vh - 35px);
    text-shadow: 1px 1px #111;
  }

  .site-nav {
    width: 25%;
  }


  .project-grid {
    grid-template-columns: 1fr 1fr;
    height: 140vh;
    background-color: #111;
    grid-gap: 1px;
    border-top-style: solid;
    border-top-width: 1px;
    border-top-color: black;
  }

  .project {
    border: none;
    background-color: #fff;
  }

  .project img {
    width: 70%;
  }

  .project p {
    width: 70%;
    padding-top: 10px;
    font-size: 14px;
  }

  .resumePage {
    height: 140vh;
  }

  .resume-container {
    width: 90%;
    height: 90%;
    margin: 0 auto;
  }

  .resume {
    width: 100%;
    height: 100%;
    cursor: default;
  }

  .project-header-body {
    line-height: 8em;
  }

  .project-header-body h2 {
    font-size: 40px;
  }

  .social-nav {
    display: inline-block;
    list-style: none;
    float: right;
    height: 100%;
    margin-right: 15px;
  }

  .social-nav li {
    float: right;
    margin: 15px;
    color: white;
    cursor: pointer;
  }

  .social-nav li a {
    color: white;
  }

  .social-nav li a:hover {
    animation-name: social-transition;
    animation-duration: 1s;
    color: #00faf2;
  }

  .footer {
    background-color: rgba(0,0,0,0);
    grid-template-columns: 1fr 1fr;
  }

  .footer div {
    padding: 20px 10px;
  }

  .footer div h4 {
    margin: 0 10px;
    color: rgba(255, 255, 255, 0.6) !important;
  }

  .footer-desktop-only{
    display: grid !important;
  }

}

@media only screen and (min-width: 1000px){
  .content {
    width: 1000px;
  }

  .header h3 {
    font-size: 70px;
  }

  .project-header-body {
    line-height: 10em;
  }

  .project-header-body h2 {
    font-size: 50px;
  }

  .project p {
    font-size: 18px;
  }

  .footer {
    background-color: rgba(0,0,0,0);
    grid-template-columns: 1fr 1fr;
  }

  .footer div {
    padding: 20px 25%;
  }

  .footer div h4 {
    margin: 0 10px;
    color: rgba(255, 255, 255, 0.6) !important;
  }

  .footer-desktop-only{
    display: grid !important;
  }
}

@media only screen and (min-width: 1400px) and (min-height: 1200px){
  .content {
    width: 1200px;
  }

  .header h3 {
    font-size: 80px;
  }

  .project-header-nav {
    height: 70px;
  }

  .site-nav li h3 {
    line-height: 70px;
    font-size: 22px;
  }

  .social-nav li {
    padding-right: 20px;
  }

  .project-header-body h2 {
    font-size: 70px;
    padding: 66px;
  }

  .project h3 {
    font-size: 26px;
    padding-top: 20px;
  }

  .project p {
    font-size: 26px;
    line-height: 46px;
    padding-top: 22px;
  }

  .project ul li {
    margin: 20px;
  }

  .project ul li a {
    font-size: 22px;
    padding: 8px;
    letter-spacing: 2px;
  }

  .footer div h4:nth-child(1){
    font-size: 18px;
  }

  .footer div h4:nth-child(2){
    font-size: 22px;
  }

  .footer div {
    padding-top: 40px;
  }
}

@media only screen and (min-width: 2200px) and (min-height: 2000){
  .content {
    width: 2200px;
  }

  .header h3 {
    font-size: 150px;
  }

  .project-header-nav {
    height: 120px;
  }

  .site-nav li h3 {
    font-size: 32px;
    line-height: 120px;
  }

  .social-nav li {
    line-height: 120px;
    padding-right: 40px;
  }

  .project-header-body h2 {
    font-size: 120px;
    padding: 120px;
  }

  .project h3 {
    font-size: 44px;
    padding-top: 40px;
  }

  .project p {
    font-size: 40px;
    line-height: 70px;
    padding-top: 30px;
  }

  .project ul li {
    margin: 30px;
  }

  .project ul li a {
    padding: 12px;
    font-size: 32px;
    letter-spacing: 2px;
  }

  .footer div h4:nth-child(1){
    font-size: 28px;
    letter-spacing: 4px;
  }

  .footer div h4:nth-child(2){
    font-size: 32px;
    letter-spacing: 2px;
  }

  .footer div {
    padding-top: 60px;
  }
}

.onPage {
  background-color: #fff;
  color: #111 !important;
}

.menuDisplay {
  margin-right: 0;
  transition: 1s;
}

.menuHidden {
  margin-right: -251px;
  transition: 1s;
}